/* Background styling */
body {
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
  background: url('./background.jfif') no-repeat center center fixed;
  background-size: cover;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Main container */
.App {
  background: rgba(255, 255, 255, 0.8); /* White with some transparency */
  padding: 40px;
  border-radius: 15px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
  text-align: center;
  max-width: 400px;
  width: 100%;
}

/* Heading */
h1 {
  font-size: 24px;
  margin-bottom: 30px;
  color: #333;
}

/* Form styling */
form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

input[type="text"] {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border: 2px solid #ddd;
  border-radius: 8px;
  font-size: 16px;
  transition: border-color 0.3s ease;
}

input[type="text"]:focus {
  border-color: #007bff;
  outline: none;
}

/* Button styling */
button {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  font-size: 16px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #0056b3;
}

/* Analysis result styling */
h3 {
  font-size: 20px;
  margin-top: 30px;
  color: #333;
}

p {
  font-size: 16px;
  margin-top: 10px;
  color: #555;
}

p.safe {
  color: #28a745; /* Green for safe */
}

p.danger {
  color: #dc3545; /* Red for danger */
}
